.container{
    background-color: blueviolet;
    color: aliceblue;
    flex: 4;
   
}
.list_items{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    
}
.list_items>div{
    display: flex;
   
    justify-content: center;

}
button{
    border-color:whitesmoke;
    font-weight: bold;
    font-size: 30px;
    color: whitesmoke;
    padding: 30px;
    margin-left: 30px;
    background-color:rgb(120, 39, 201);
    border-radius: 20px;
}
.arr{
    background-color:rebeccapurple;
    color: whitesmoke;
    text-decoration: none;
    border-radius: 10px;
    margin-bottom: 10px;
    border-color: red;
    padding: 20px;
    border-color: whitesmoke;
}
.item_box>ul{
  list-style-type:none;
    
}